// Book Preview
.book-preview {
	display: inline-block;
	max-width:400px;
	width:100%;
	position: relative;
}

.book-preview .background-device {
	width:100%;
	position: absolute;
}

/* Owl Slider */
.owl-book {
	width:80%;
	display: inline-block;
	margin:0 auto;
}

.owl-book .book-item {
	box-shadow:0px 0px 1px rgba(0,0,0,.2);
	margin-top: 48px;
	position: relative;
}

.owl-book .book-item .overlay {
	position: absolute;
	left:0;
	top:0;
	width:100%;
	height: 100%;
	opacity: 0;
	transition: all 0.3s ease;
	background: rgba(0,0,0,0.7);
}
.owl-book  .book-item:hover .overlay {
	opacity: 1;
}
.owl-book .book-item img {
	width:100%;
	// margin:15% auto;
	transition: all 0.3s ease;
	border-radius:4px;
}
.owl-book .book-item:hover img {
}
.owl-book .book-item .overlay > a {
	position: absolute;
	display: inline-block;
	left:calc(50% - 30px);
	top:calc(50% - 25px);
	font-size:24px;
	line-height: 50px;
	width:50px;
	height: 50px;
	text-align: center;
	background: white;
	border-radius:50%;
	box-shadow:0px 1px 3px rgba(0,0,0,.3);
	color:$secondary-color;
}

.owl-book .book-item .overlay > a:hover {
	color:white;
	background: $primary-color;
}

.owl-reviews .book-item {
	padding:0 15px;
}

.owl-reviews .owl-controls {
	position: absolute;
	width:100%;
	top:calc(50% - 30px);
}

.owl-reviews .owl-controls .owl-prev,
.owl-reviews .owl-controls .owl-next {
	position: absolute;
	font-size:36px;
	color:#bbb;
	transition: all 0.3s ease;
}
.owl-reviews .owl-controls .owl-prev:hover,
.owl-reviews .owl-controls .owl-next:hover {
	color:#484d53;
}
.owl-reviews .owl-controls .owl-prev {
	left:-40px;
}
.owl-reviews .owl-controls .owl-next {
	right:-40px;
}

@media(max-width:768px) {
	.owl-reviews .owl-controls {
		position: relative;
		text-align: center;
		top:-30px;
	}

	.owl-reviews .owl-controls .owl-prev,
	.owl-reviews .owl-controls .owl-next {
		position: relative;
		display: inline-block;
		font-size:36px;
	}
	.owl-reviews .owl-controls .owl-prev {
	}
	.owl-reviews .owl-controls .owl-next {
	}
}




