// others book


.book-item{
	position: relative;

	&:hover .hover-item{
		opacity: 1;
		visibility: visible;
	}

	.hover-item{
		position: absolute;
		content:"";
		width: 100%;
		height:100%;
		background: rgba(0,0,0,0.6);
		left: 0px;
		top:0px;
		display: flex;
		justify-content: center;
		align-items: center;
		opacity: 0;
		visibility: hidden;
		-webkit-transition: all .35s ease;
		   -moz-transition: all .35s ease;
		    -ms-transition: all .35s ease;
		     -o-transition: all .35s ease;
		        transition: all .35s ease;

		i{
			width: 60px;
			height:60px;
			background: $light;
			color: $black;
			text-align: center;
			display: inline-block;
			border-radius: 100%;
			padding-top: 20px;
			-webkit-transition: all .35s ease;
			   -moz-transition: all .35s ease;
			    -ms-transition: all .35s ease;
			     -o-transition: all .35s ease;
			        transition: all .35s ease;
			&:hover{
				background: $primary-color;
				color: $light;
			}

		}
	}
}


/*******************************
* Does not work properly if "in" is added after "collapse".
*******************************/
    .panel-group .panel {
        border-radius: 0;
        box-shadow: none;
        border-bottom:1px solid rgb(235, 235, 235);
    }

    .panel-default > .panel-heading {
        padding: 0;
        border-radius: 0;
        color: #212121;
    }

    .panel-title {
        font-size: 20px;
        font-weight: 400;
    }

    .panel-title > a {
        display: block;
        padding: 15px;
        text-decoration: none;
    }

    .more-less {
        float: right;
        color: #212121;
    }

    .panel-default > .panel-heading + .panel-collapse > .panel-body {
        border-top-color: #EEEEEE;
    }

    .panel-body{
    	padding: 20px;
    }

